body {
  font-family: "Poppins", sans-serif;
  background: #fff;
  user-select: none;
}
a:link {
  text-decoration: none;
}

/* ------> Header CSS */

.logo {
  height: 38px;
  opacity: 0.85;
  font-weight: 700;
  color: var(--bs-dark);
}

.hireMeBtn {
  transition: 0.5s linear;
  font-weight: 700;
}
.hireMeBtn:hover {
  background: var(--bs-white);
  border: 1px solid var(--bs-dark);
  color: var(--bs-dark);
}

/* -------> /Header */

/* -------> Landing */

.heroTitle {
  font-size: 3.1rem;
  font-weight: 900;
  color: var(--bs-dark);
}
.heroDescription {
  opacity: 0.7;
  font-weight: 500;
}
.btn {
  font-weight: 700;
}
/* /Landing CSS */
.whyTitle {
  font-weight: 800;
  opacity: 0.8;
}
.whyT {
  font-weight: 700;
  opacity: 0.9;
}
.whyP {
  font-weight: 500;
  opacity: 0.7;
}
.numberBox {
  border-radius: 12px;
  margin: 1rem;
  background: #f8f9fa;
}
.lead {
  opacity: 0.7;
  font-weight: 500;
}

/* What */

.dShadow {
  border: 3px solid #5468ff;
  border-radius: 50%;
}

/* Reviews CSS */
.br50 {
  border-radius: 50%;
}
.reviewRow {
  background: #0a0a15;
}

.reviewTitle {
  color: #fff;
  font-weight: 800;
}

.reviewAlert {
  cursor: pointer;
  min-height: 30vh;
  color: #fff;
  border-radius: 10px;
  background: #212529;
  opacity: 0.95;
  min-height: 36vh;
}

.reviewerText {
  font-weight: 600;
  font-size: 20px;
}
.reviewText {
  opacity: 0.75;
  font-size: 15px;
  font-weight: 300;
  letter-spacing: 1px;
}
.reviewerCountryText {
  font-size: 15px;
  font-weight: 500;
  opacity: 0.9;
}
.mapIcon {
  opacity: 0.8;
}
@media (max-width: 320px) {
  .cus-col-xs-5 {
    display: flex;
    justify-content: center;
  }
  .cus-col-xs-7 {
    width: 100vw;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .reviewAlert {
    min-height: 20vh;
  }
}

/* /Reviews CSS */
/* Add Review CSS */
.addReviewRow {
  background: #0a0a15;
}

.addReviewtitle {
  color: #ffffff;
  font-weight: 800;
}
.description {
  color: #fff;
  opacity: 0.6;
}
.googleLoginBtn {
  border: none;
  background: var(--bs-white);
}
/* /Add Review CSS */

/* Form */
.txtfff {
  color: #fff;
}
/* /From */
/* Arrow Animation */
/* Arrow animation CSS */
.arrowBtn {
  box-shadow: none;
  outline: none;
}

.arrowBtn:focus {
  box-shadow: none;
}

.bounce {
  -moz-animation: bounce 3s infinite;
  -webkit-animation: bounce 3s infinite;
  animation: bounce 3s infinite;
}
@-moz-keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -moz-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -moz-transform: translateY(-30px);
    transform: translateY(-30px);
  }
  60% {
    -moz-transform: translateY(-15px);
    transform: translateY(-15px);
  }
}
@-webkit-keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-30px);
    transform: translateY(-30px);
  }
  60% {
    -webkit-transform: translateY(-15px);
    transform: translateY(-15px);
  }
}
@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -moz-transform: translateY(-30px);
    -ms-transform: translateY(-30px);
    -webkit-transform: translateY(-30px);
    transform: translateY(-30px);
  }
  60% {
    -moz-transform: translateY(-15px);
    -ms-transform: translateY(-15px);
    -webkit-transform: translateY(-15px);
    transform: translateY(-15px);
  }
}
/* ---------- Hover CSS ----------- */

/* Buzz Out */
@-webkit-keyframes hvr-buzz-out {
  10% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }
  20% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }
  30% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }
  40% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }
  50% {
    -webkit-transform: translateX(2px) rotate(1deg);
    transform: translateX(2px) rotate(1deg);
  }
  60% {
    -webkit-transform: translateX(-2px) rotate(-1deg);
    transform: translateX(-2px) rotate(-1deg);
  }
  70% {
    -webkit-transform: translateX(2px) rotate(1deg);
    transform: translateX(2px) rotate(1deg);
  }
  80% {
    -webkit-transform: translateX(-2px) rotate(-1deg);
    transform: translateX(-2px) rotate(-1deg);
  }
  90% {
    -webkit-transform: translateX(1px) rotate(0);
    transform: translateX(1px) rotate(0);
  }
  100% {
    -webkit-transform: translateX(-1px) rotate(0);
    transform: translateX(-1px) rotate(0);
  }
}
@keyframes hvr-buzz-out {
  10% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }
  20% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }
  30% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }
  40% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }
  50% {
    -webkit-transform: translateX(2px) rotate(1deg);
    transform: translateX(2px) rotate(1deg);
  }
  60% {
    -webkit-transform: translateX(-2px) rotate(-1deg);
    transform: translateX(-2px) rotate(-1deg);
  }
  70% {
    -webkit-transform: translateX(2px) rotate(1deg);
    transform: translateX(2px) rotate(1deg);
  }
  80% {
    -webkit-transform: translateX(-2px) rotate(-1deg);
    transform: translateX(-2px) rotate(-1deg);
  }
  90% {
    -webkit-transform: translateX(1px) rotate(0);
    transform: translateX(1px) rotate(0);
  }
  100% {
    -webkit-transform: translateX(-1px) rotate(0);
    transform: translateX(-1px) rotate(0);
  }
}
.hvr-buzz-out {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}
.hvr-buzz-out:hover,
.hvr-buzz-out:focus,
.hvr-buzz-out:active {
  -webkit-animation-name: hvr-buzz-out;
  animation-name: hvr-buzz-out;
  -webkit-animation-duration: 0.75s;
  animation-duration: 0.75s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
}
